.prize {
  aspect-ratio: 39 / 89;
  z-index: 1;
  background-image: url("prize_logo.d32e0234.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-clip: padding-box;
  border-radius: .2rem;
  width: 8lvw;
  min-width: 100px;
  height: auto;
  min-height: auto;
  margin: 2rem;
  transition: all .3s;
  display: inline-block;
  position: absolute;
  bottom: 0;
  overflow: hidden;
  scale: 1;
  box-shadow: 0 0 16px #25131721, 0 0 10px #2e151a2e, 0 0 3px #361c211f;
}

.prize:after {
  content: "";
  z-index: 1;
  background: linear-gradient(210deg, #00000070 0%, #0000 40%);
  border-radius: .2rem;
  transition: background .3s;
  position: absolute;
  inset: 0;
}

.prize:hover {
  scale: 1.003;
}

.prize:hover:after {
  background: linear-gradient(210deg, #0000 0%, #ede9e720 100%);
}

@media screen and (width <= 424px) {
  .prize {
    min-width: 80px;
    margin: 1rem;
  }
}
